.section-services {
  background-color: #fff;
  color: #333;
  padding-bottom: 40px;

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(410px, 1fr));
    gap: 30px;
  }

  &__left {
    grid-column: 1/3;
  }

  &__right {
    grid-column: 3/4;
  }

  & a {
    transition: all .2s linear;
  }

  & a>svg {
    transition: all .2s linear;
  }

  & a:hover {
    color: #CE4307;
  }

  & a:hover>svg path {
    fill: #CE4307;
  }
}

.tab {
  display: none;

  &__btns {
    display: flex;
    
  }

  &__btn:not(:last-child) {
    margin-right: 128px;
   
  }

  &__btn.active {
    color: #ff6e30;
  }

  &__btn {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 28px;
    color: #666666;
    cursor: pointer;
  }
}

.tab-scroll.active {
  display: grid;
}

.tab-scroll {
  display: none;
  grid-template-columns: repeat(auto-fill, minmax(847px, 1fr));
  grid-template-rows: repeat(5, 1fr);
  margin: 0 auto;
  gap: 25px;

  &__content {
    padding-top: 19px;
  }

  &__item {
    display: flex;

    &__img img {
      min-height: 100%;
    }
  }

  &__content-title {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 23px;
    color: #202020;
    margin-bottom: 7px;
  }

  &__content-text {
    margin: 0;
    margin-bottom: 19px;
    color: #999999;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    max-width: 521px;
  }
}

.tab-scroll__content>a,
.tab-retouch__content>a {
  display: flex;
  align-items: center;
  font-family: Roboto;
  font-weight: 700;
  font-size: 16px;
  line-height: 28px;
  color: #ff6e30;
}

.tab-retouch.active {
  display: grid;
}

.tab-retouch {
  display: none;
  grid-template-columns: repeat(auto-fill, minmax(410px, 1fr));
  grid-template-rows: repeat(5, 1fr);
  margin: 0 auto;
  gap: 20px;

  &__item {
    display: flex;
  }

  &__content-title {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: #202020;
    max-width: 192px;
    margin-bottom: 10px;
  }
}

.tab-scroll__img {
  margin-right: 29px;
}

.tab-retouch__img {
  margin-right: 30px;
}

.section-services__promotions {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  background-image: url("../../img/services/promotion.jpg");
  min-height: 490px;
}

.promotions {
  margin-top: -13px;
  color: #fff500;
  padding: 20px 30px;
  margin-bottom: 29px;

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 104%;
    margin-bottom: 2px;
  }

  &__note {
    margin: 0;
    font-family: Roboto;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
  }

  &__price {
    margin: 0;
    max-width: 218px;
    font-family: Roboto;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
    margin-bottom: 9px;
  }

}

.works {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(60px, 1fr));
  grid-template-rows: repeat(4, 1fr);
  margin: 0 auto;
  gap: 30px;
}

.section-services__right {
  margin-top: -25px;
}

.tabs__btns {
  display: inline-block;
  max-width: 903px;
  border-bottom: 4px solid #c4c4c4;
  margin-bottom: 22px;
}

.tab__btn {
  height: 100%;
  display: inline-block;
  font-family: Roboto;
  font-weight: 700;
  font-size: 16px;
  line-height: 28px;
}

.section-services__right h4 {
  margin: 0;
  margin-bottom: 13px;
}

.tab__btn.active {
  position: relative;
}

.tab__btn.active::after {
  position: absolute;
  content: '';
  display: block;
  height: 4px;
  background-color: #ff6e30;
  width: 100%;
  top: 100%;
  left: 0;
}

.tab__btn:hover {
  color: #ff6e30;
}

.tab__btn:focus-visible {
  background-color: #FFD4C1;
}

.tab-scroll__content:hover>.tab-scroll__content-title {
  color: #ff6e30;
}

@media (max-width: 1340px) {
  .section-services__wrapper {
    grid-template-columns: repeat(auto-fill, minmax(355px, 1fr));
    gap: 28px;
  }

  .tab-scroll {
    grid-template-columns: repeat(auto-fill, minmax(591px, 1fr));

  }

  .tab-retouch.active {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }

  .tab-retouch__item {
    flex-direction: column;
    max-height: 234px;
  }

  .tab-retouch__item img {
    object-fit: cover;
  }

  h3.tab-retouch__content-title {
    max-width: 100%;

  }

  .tab-retouch__content-title span {
    display: none;
  }

}

@media (max-width: 1140px){
.section-services__wrapper {
    grid-template-columns: repeat(auto-fill, minmax(322px, 1fr));
    
}
}

